<template>
    <div>
        <div class="goods">
            <div class="menu-wrapper" ref="menuWrapper">
                <ul>
                    <li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectMenu(index,$event)">
                        <span class="text border-1px">
						<span v-if="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
                        </span>
                    </li>
                </ul>
            </div>
            <div class="foods-wrapper" ref="foodsWrapper">
                <ul>
                    <li v-for="item in goods" class="food-list food-list-hook" ref="foodList">
                        <h1 class="title">{{item.name}}</h1>
                        <ul>
                            <li v-for="food in item.foods" class="food-item border-1px" @click="selectFood(food,$event)">
                                <div class="icon">
                                    <img width="57" height="57" :src="food.icon" alt="">
                                </div>
                                <div class="content">
                                    <h2 class="name">{{food.name}}</h2>
                                    <p class="desc">{{food.description}}</p>
                                    <div class="extra">
                                        <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                                    </div>
                                    <div class="price">
                                        <span class="now">￥{{food.price}}</span><span class="old" v-if="food.oldPrice">￥{{food.oldPrice}}</span>
                                    </div>
                                    <div class="cartcontrol-wrapper">
                                        <cartcontrol @add="addFood" :food="food"></cartcontrol>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
            <shopcart ref="shopcart" :selectFoods="selectFoods" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice"></shopcart>
        </div>
        <food @add="addFood" :food="selectedFood" ref="food"></food>
    </div>
</template>
<script type="text/javascript">
	import BScroll from 'better-scroll';
	import shopcart from '../shopcart/shopcart.vue';
	import cartcontrol from '../cartcontrol/cartcontrol.vue';
	import food from '../food/food.vue';

	const ERR_OK=0;

	export default {
		props:{
			seller:{
				type:Object
			}
		},
		data() {
			return {
				goods:[],
				listHeight:[],
				scrollY:0,
				selectedFood:{}
			}
		},
		computed:{
			currentIndex(){
				for(let i=0;i<this.listHeight.length;i++){
					let height1=this.listHeight[i];
					let height2=this.listHeight[i+1];
					if(!height2||(height1<=this.scrollY&&height2>this.scrollY)){
						return i;
					}
				}
				return 0;
			},
			selectFoods() {
				let foods=[];
				this.goods.forEach((good)=>{
					good.foods.forEach((food)=>{
						if(food.count){
							foods.push(food);
						}
					})
				})
				return foods;
			}
		},
		created(){
			this.$http.get("/api/goods").then((res)=>{
				res=res.body;
				if(res.errno===ERR_OK){
					this.goods=res.data;
					this.$nextTick(()=>{
						this._initScroll();
						this._calculateHeight();
					})
				}
			})
			this.classMap=['decrease','discount','special','invoice','guarantee'];
		},
		methods:{
			_initScroll() {
				/*
				new BScroll(第一个参数为dom节点,{
					此处为option参数
				})
				*/
				this.menuScroll=new BScroll(this.$refs.menuWrapper,{
					//是否启用点击事件
					click:true
				});

				this.foodsScroll=new BScroll(this.$refs.foodsWrapper,{
					click:true,
					probeType:3
					//probeType: 1 监听事件的触发时间，1为即时触发，3为延迟到事件完毕后触发
				});

				this.foodsScroll.on("scroll",(pos)=>{
					//首先使用Math.round()对取到的pos.y进行四舍五入，然后使用Math.abs()取得绝对值
					this.scrollY=Math.abs(Math.round(pos.y));   //获取滚动时顶部对于初始时的Y轴偏移量
				});
			},
			_calculateHeight() {
				let foodList=this.$refs.foodList;
				let height=0;
				this.listHeight.push(height);
				for(let i=0;i<foodList.length;i++){
					let item=foodList[i];
					//clientHeight获取元素的高度
					height+=item.clientHeight;
					this.listHeight.push(height);
				}
			},
			selectMenu(index,event) {
				if(!event._constructed){
					return;
				}
				let foodList=this.$refs.foodList;
				let el=foodList[index];
				this.foodsScroll.scrollToElement(el,300);
			},
			selectFood(food,event) {
				if(!event._constructed){
					return;
				}
				this.selectedFood=food;
				this.$refs.food.show();
			},
			addFood(target) {
			  this._drop(target);
			},
			_drop(target) {
			  // 体验优化,异步执行下落动画
			  this.$nextTick(() => {
			    this.$refs.shopcart.drop(target);
			  });
			}
		},
		components:{
			shopcart,
			cartcontrol,
			food
		}
	};
</script>
<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixin.styl"
	.goods
		display: flex
		position:absolute
		top:174px
		bottom:46px
		width:100%
		overflow:hidden
		.menu-wrapper
			flex:0 0 80px
			width:80px      //安卓浏览器兼容问题
			background:#f3f5f7
			ul
				.menu-item
					display: table
					width:56px
					height:54px
					padding:0 12px
					font-size:12px
					line-height:14px
					vertical-align:middle
					&:last-of-type
						&.current
							padding-bottom:1px
						.text
							border-none()
					&.current
						position: relative
						z-index: 10
						margin-top: -1px
						background: #fff
						font-weight: 700
						.text
							border-none()
					.text
						display: table-cell
						height:100%
						vertical-align:middle
						&.border-1px
							border-1px(rgba(7,17,27,.1))
						.icon
							vertical-align:top
							display: inline-block
							width:12px
							height:12px
							margin-right:2px
							background-size:12px 12px
							background-repeat:no-repeat
							&.decrease
								bg-image('decrease_3')
							&.discount
								bg-image('discount_3')
							&.guarantee
								bg-image('guarantee_3')
							&.invoice
								bg-image('invoice_3')
							&.special
								bg-image('special_3')
		.foods-wrapper
			flex:1
			font-size:12px
			.food-list
				.title
					height:26px
					line-height:26px
					background:#f3f5f7
					border-left:2px solid #d9dde1
					padding-left:14px
					color:rgb(147,153,159)
				ul
					.food-item
						display: flex
						margin:18px
						padding-bottom:18px
						border-1px(rgba(7,17,27,.1))
						&:last-child
							border-none()
							margin-bottom:0
						.icon
							flex:0 0 57px
							margin-right:10px
						.content
							flex:1
							.name
								margin:2px 0 8px
								height:14px
								line-height:14px
								font-size: 14px
								color:rgb(7,17,27)
							.desc,.extra
								line-height: 10px
								font-size: 10px
								color: rgb(147, 153, 159)
							.desc
								line-height: 12px
								margin-bottom: 8px
							.extra
								.count
									margin-right: 12px
							.price
								font-weight: 700
								line-height: 24px
								.now
									margin-right:8px
									font-size: 14px
									color:rgb(240,20,20)
								.old
									text-decoration: line-through
									font-size: 12px
									color:rgb(147,153,159)
						.cartcontrol-wrapper
							position: absolute
							right: 0
							bottom: 12px
</style>


